//library
import Vue from 'vue'

//vue file
import App from './App'

//router
import router from './router'

//scss
import "./assets/scss/App.scss"
import "./assets/scss/BMI.scss"

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router:router,
  components: { App },
  template: '<App/>'
})



