<template>
  <div id="BMI">

    <div id="inputDisplay">
      <div> Height : {{ height }}cm</div><!-- 3:変数を表示 -->
      <div> Weight : {{ weight }}kg</div>
    </div>

    <div id="inputBMI">
      <div>Input Height(cm) ->
        <!-- 2:変数を変更する @inputで入力した時の処理を設定 -->
        <input type="text" pattern="^[0-9]*" maxlength="3" :value="height" @input="height=opt(height,$event),update()">
      </div>
      <div>Input Weight(kg) ->
        <input type="text" pattern="^[0-9]*" maxlength="3" :value="weight" @input="weight=opt(weight,$event),update()">
      </div>
    </div>

    <div id="resultDisplay">
      <div :class="{'less' : bmiState==0, 'just':bmiState==1, 'over':bmiState==2}" >BMI : {{ BMI }}</div>
      <div>適正体重 : {{ properWeight }}</div>
    </div>

  </div>
</template>

<script>
  export default{
    data(){
        return{
            //1:変数の設定
            height:0,
            weight:0,
            BMI:0,
            properWeight:0,

            bmiState:1,

            resultStyle:{
              "color":"red"
            }
        }
    },

    methods: {
        opt:function(oldVal,e){
            var re = new RegExp(e.target.pattern);
            var result = re.exec(e.target.value);
            return e.target.value = (result)?result[0]:oldVal;
        },
        update(){
          this.BMI = (Number(this.weight) / (Number(this.height)/100)**2).toFixed(2),
          this.properWeight = ((Number(this.height)/100)**2 * 22).toFixed(2)

          if(Number(this.BMI) < 18.5){
            this.bmiState = 0
          }else if(Number(this.BMI > 25)){
            this.bmiState = 2
          }else{
            this.bmiState = 1
          }

        }
        
    },
  }
</script>



