//library
import Vue from 'vue'
import VueRouter from 'vue-router'

//path file
import Home from './components/Home.vue'
import BMI from './components/BMI.vue'

Vue.use(VueRouter)

var router = new VueRouter({
    mode:'history',
    routes: [
        {
            path:'/',
            component:Home
        },
        {
            path:'/bmi',
            component:BMI
        }
    ]
})

export default router;


